import React, { useState, useEffect } from 'react'
import TodoItem from './TodoItem'
import { v4  } from 'uuid'

function Todo() {
    const [valueInput, setValueInput] = useState('')
    const [todos, setTodos] = useState([]);
    const [isEmpty, setIsempty] = useState(false)
    
    useEffect(() => {
        setTodos(JSON.parse(localStorage.getItem('todos')) || [])
    }, [])

    const onChangeInput = (e) => {
        setValueInput(e.target.value)
    }

    const addTodo = (e) => {
        e.preventDefault()
        if(!valueInput) {
            setIsempty(true)
            return
        }
        setIsempty(false)
        setTodos([{id: v4(), title: valueInput, isComplete: false}, ...todos ])
        localStorage.setItem('todos', JSON.stringify([{id: v4(), title: valueInput, isComplete: false}, ...todos ]))
        setValueInput('')
    }

    const delTodo = (id) => {
        const newTodos = todos.filter(todo => todo.id !== id)
        setTodos(newTodos)
        localStorage.setItem('todos', JSON.stringify(newTodos))
    }
    const setCheckbox = (id) => {
        const newTodos = todos.map(todo => {
            if(todo.id === id){
                return {...todo, isComplete: !todo.isComplete}
            }
            return todo
        })
        setTodos(newTodos)
        localStorage.setItem('todos', JSON.stringify(newTodos))
    }
    return (
        <div className="todo">
            <h2 className="todo-title">Todo App</h2>
            <form className="todo-form" onSubmit={addTodo}>
                <input type="text" placeholder="Add your new todo" value={valueInput} onChange={onChangeInput}/>
                <div className="btn-add" onClick={addTodo}>
                    <i className="fa fa-plus" aria-hidden="true"></i>
                </div>
            </form>
            {isEmpty ? <p className="warn-empty">Please enter your todo!</p>: ''}
            <div className="todo-list">
                {todos.map(todo => <TodoItem title={todo.title} key={todo.id} delTodo={delTodo} id={todo.id} isComplete={todo.isComplete} setCheckbox={setCheckbox}/>)}
            </div>
        </div>
    )
}

export default Todo
