import React from 'react'

function TodoItem({ title, delTodo, id, isComplete, setCheckbox }) {
    return (
        <div className="todo-item">
            <input type="checkbox" onChange={() => setCheckbox(id)} checked={isComplete ? "checked" : ''}/>
            <h5 className={isComplete ? 'line' : 'dac'}>{title}</h5>
            <div className="btn-del" onClick={() => delTodo(id)}><i className="fa fa-trash" aria-hidden="true"></i></div>
        </div>
    )
}

export default TodoItem
